.myslider {
    width: 80%;
    height: 240px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    background: #FFFFFF;
    
}

.slick_slider {
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.myslider__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
}

.myslider__item h3 {
    font-family: 'Ladoga';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    color: #000000;
    margin: 0 auto;
    width: 80%;
    opacity: 0.5;
}

.myslider__item span {
    font-family: 'Romanovsky';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    text-transform: uppercase;
    display: block;
    margin-top: 20px;
    color: #000000;
}