.biblio {
    width: 80%;
    margin: 0 auto;
    padding-top: 70px;
    background-color: #FFFFFF;
}

.biblio__container {
    display: flex;
    padding-left: 40px;
    padding-right: 40px;
}

.biblio__image {
    position: relative;
    width: 786px;
    height: 500px;
}

.biblio__shadow {
    position: absolute;
    background-blend-mode: screen;
    width: 100%;
    height: 100%;
    background: rgba(74, 144, 226, 0.3);
}

.biblio__img {
    width: 100%;
    height: 100%;
}

.biblio__group {
    margin-left: 50px;
}

.biblio__bibliography {
    display: flex;
    align-items: center;
    margin-top: 20px;
}

.biblio__bibliography p {
    margin: 0;
    padding: 0;
    font-family: 'Romanovsky';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #000000;
}

.biblio__bibliography span {
    margin: 0;
    padding: 0;
    margin-left: 20px;
    font-family: 'Romanovsky';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #9B9B9B;
}

.biblio__quotes {
    margin-top: 20px;
}

.biblio__quotes p {
    width: 325px;
    margin: 0;
    padding: 0;
    font-family: 'Ladoga';
    font-style: normal;
    font-weight: 400;
    font-size: 35px;
    line-height: 35px;
    color: #4A90E2;
}