.navigation {
   width: 100%;
}

.navigation__block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0;
    padding: 0;
}

.navigation__item {
    list-style-type: none;
   
}

.navigation__link {
    font-family: 'LadogaDisplay';
    font-style: normal;
    font-size: 18px;
    line-height: 18px;
    color: #000000;
    text-decoration: none;
}
.navigation__link:hover {
    color: #4A90E2;
    border-bottom: 1px solid #4A90E2;
    padding-bottom: 21px;

}

.active {
    color: #4A90E2;
    border-bottom: 1px solid #4A90E2;
    padding-bottom: 21px;
}