.header {
    width: 80%;
    height: 200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    border-bottom: 1px solid #ECECEC;
}

.header__nav {
    width: 65%;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.header__search {
    width: 160px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #ECECEC;
}

.header__logo {
    margin-top: 60px;
}

.header__image {
    width: 160px;
    height: 100%;
}

.header__img {
    width: 100%;
    height: 100%;
}